import React, { Fragment } from "react";
import mernstac1 from "../../styles/assets/images/mernstack/mernstac1.jpg";
import mernstac2 from "../../styles/assets/images/mernstack/mernstac2.jpg";
import mernstac3 from "../../styles/assets/images/mernstack/mernstac2.jpg";
import { Carousel } from "react-bootstrap";

const MernDevelopmentServices = () => {
  return (
    <Fragment>
      <div className="business-wrap my-5 pt-5">
        <div className="container-fluid">
          <div className="business-need p-5 row">
            <div className="col-12">
              <div className="mx-5">
                <h3 className="mb-2 fw-bold">
                  MEAN/MERN Stack Development Services
                </h3>
                <p className="text-dark fw-semibold fs-3 mt-4">
                  At Justtry Technologies, we understand the significance of
                  building responsive, efficient, and feature-rich web
                  applications. That's where our expertise in MERN (MongoDB,
                  Express.js, React, Node.js) and MEAN (MongoDB, Express.js,
                  Angular, Node.js) stack development comes into play.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <main>
        <div className="container merncarousel">
          <div className="d-flex align-items-center row">
            <div className="col-md-12">
              <Carousel>
                <Carousel.Item>
                  <div className="d-flex align-items-center row">
                    <div className="col-md-6">
                      <div className="fs-5 fw-semibold p-5">
                        <p className="fs-4 fw-semibold">
                          Our seasoned developers are passionate about crafting
                          seamless user experiences, driven by a commitment to
                          innovation and excellence. Whether it's creating
                          interactive front-end interfaces using React or
                          Angular, designing robust server-side solutions with
                          Node.js, or implementing flexible, NoSQL databases
                          like MongoDB, we've got you covered.
                        </p>
                      </div>
                    </div>
                    <div className="col-md-5">
                      <img
                        className="d-block w-100"
                        src={mernstac1}
                        alt="mernstack-1-img"
                      />
                    </div>
                    <div className="col-md-1"></div>
                  </div>
                </Carousel.Item>
                <Carousel.Item>
                  <div className="d-flex align-items-center row">
                    <div className="col-md-6">
                      <div className="mern-content">
                        <p className="fs-4 fw-semibold">
                          What sets us apart is our client-centric approach. We
                          collaborate closely with you to understand your unique
                          goals, ensuring every line of code we write aligns
                          perfectly with your vision. Our solutions are not just
                          about code; they're about enhancing your brand,
                          boosting user engagement, and achieving tangible
                          business results.
                        </p>
                      </div>
                    </div>
                    <div className="col-md-5">
                      <img
                        className="d-block w-100"
                        src={mernstac2}
                        alt="mernstack-2-img"
                      />
                    </div>
                    <div className="col-md-1"></div>
                  </div>
                </Carousel.Item>
                <Carousel.Item>
                  <div className="d-flex align-items-center row">
                    <div className="col-md-6">
                      <div className="mern-content">
                        <p className="fs-4 fw-semibold">
                          Partner with us to unlock the full potential of
                          MERN/MEAN stack development. Let's turn your ideas
                          into exceptional digital experiences. Join us at
                          Justtry technologies and let's embark on this journey
                          together.
                        </p>
                      </div>
                    </div>
                    <div className="col-md-5">
                      <img
                        className="d-block w-100"
                        src={mernstac3}
                        alt="mernstack-3-img"
                      />
                    </div>
                    <div className="col-md-1"></div>
                  </div>
                </Carousel.Item>
              </Carousel>
            </div>
          </div>
        </div>
      </main>
    </Fragment>
  );
};

export default MernDevelopmentServices;
