import Layout from "../components/layout";
import React from "react";
import MernDevelopmentScreen from "../screens/mern-development";
import Seo from "../components/seo";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import MoveForward from "../components/common/move-forward";
import BreadCrumb from "../components/common/breadcrumb";

const MernDevelopment = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "MEAN/MERN Development Services", link: null },
  ];

  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <MernDevelopmentScreen />
      <MoveForward db={db} />
    </Layout>
  );
};

export const Head = () => (
  <Seo
    title="MERN Stack Development Services"
    headTitle="MERN Stack Development Company"
    description="Maximize innovation with Justtry Technologies. We offer MERN/MEAN stack development services for robust and scalable solutions. Elevate your web applications with our expertise in cutting-edge JavaScript frameworks.
    "
    keywords="MERN Stack Development Services, MERN Stack Solutions, MongoDB Development, Express.js Development, React.js Development, Node.js Development, MERN Stack Web Application, Single Page Application (SPA) Development, Full-Stack JavaScript Development, MERN Stack API Development, MongoDB Database Design, MERN Stack E-commerce Development, Real-Time Application Development (MERN), MERN Stack Consulting Services, MERN Stack Migration Services, MERN Stack Maintenance and Support, Responsive Web Development with MERN Stack, Cross-Platform MERN Application Development, MERN Stack Performance Optimization, MERN Stack Security Implementation, MEAN Stack Development Services, MEAN Stack Solutions, MongoDB Development, Express.js Development, Angular.js Development, Node.js Development, MEAN Stack Web Application, Single Page Application (SPA) Development, Full-Stack JavaScript Development, MEAN Stack API Development, MongoDB Database Design, MEAN Stack E-commerce Development, Real-Time Application Development (MEAN), MEAN Stack Consulting Services, MEAN Stack Migration Services, MEAN Stack Maintenance and Support, Responsive Web Development with MEAN Stack, Cross-Platform MEAN Application Development, MEAN Stack Performance Optimization, MEAN Stack Security Implementation"
  ></Seo>
);

export default MernDevelopment;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
